.ulTabsWrapper{
    list-style-type: none;
}

.liTabs {
    display: inline;
    cursor: pointer;
    color: black;
    font-weight: 1000;
    font-family: 'Roboto', sans-serif;
    padding: 5px;
    transition: 0.2s;
    align-self: center;
}

.liTabs:hover{
    font-size: 1.1rem;
    transition: 0.3s;
}